// colors
$blue: #1b87c8;
$white: #fff;
$green: #6bc324;
$violet: #785777;
$maline: #ff6565;
$grey: #3f4040;
$ash: #525252;
$dark: #000;
$body-base-color: #eceff1;
// fonts
$font-family-base: "RobotoCondensed-Regular", sans-serif;
$font-weight-light: 300;
$font-weight-normal: 400;
$font-weight-bold: 700;

// transition
$transition-base: .33s;
$transition-fast: .2s;
$transition-slow: .8s;

// other
$border-radius-base: 5px;