$max-font-size: 36;
@for $i from 1 through 6{
  h#{$i}{
    font-size: em($max-font-size / $i);
    }
  }
p{
  font-size: em(16);
}
span{
  color: $dark;
}
a{
    text-decoration: none !important;
    color: $blue;
}
.author{
    font-family: "DancingScript-Bold", sans-serif;
    font-size: em(48);
}