$sign-x: 320px;
$sign-y: 100px;
$sign-frames: 112;
$sign-duration: 6.6s;
$sign-delay: 1s;

#dex-sign{
    display: inline-block;
    width: $sign-x;
    height: $sign-y;
    background-image: url(../img/14.png);
    background-repeat: no-repeat;
    &.play{
        animation: sign-anim $sign-duration $sign-delay steps($sign-frames) forwards;
    }
}

a#dex-sign{
    opacity: .9;
    &:hover{
        opacity: 1;
        filter: invert(30%) brightness(80%) sepia(100%) contrast(110%) saturate(953%);
    }
}

@keyframes sign-anim{
//    from { background-position: 0 0; }
    to{ background-position: 0 $sign-frames * -$sign-y; }
}